<template>
    <mt-tabbar v-model="selected" fixed>
        <mt-tab-item id="home">
            <img slot="icon" :src="iconHome">
            课程
        </mt-tab-item>
        <mt-tab-item id="class">
            <img slot="icon" :src="iconClass">
            学堂
        </mt-tab-item>
        <mt-tab-item id="store">
            <img slot="icon" :src="iconStore">
            商城
        </mt-tab-item>
    </mt-tabbar>
</template>

<script>
import { Tabbar } from 'mint-ui';
import utils from '../libs/utils'

export default {
  props: ['page'],
  mounted: function(){
      this['icon'+utils.firstCharUpperCase(this.page)] = require('../assets/images/' + this.page + '-active.png')
  },
  data() {
      return {
          selected: this.page,
          iconHome: require('../assets/images/home.png'),
          iconClass: require('../assets/images/class.png'),
          iconStore: require('../assets/images/store.png'),//直接写webpack不能编译
      };
  },
  watch: {
      selected(val){
        this.$router.push({
          path: val
        });
      }
  }

}
</script>

<style lang="css">
</style>



