<template>
  <mt-popup v-model="showPicker" position="bottom" popup-transition="popup-fade">
    <div class="page-picker-wrapper">
      <div class="toolbar" @click="sure">确定</div>
      <mt-picker :slots="addressSlots" @change="onAddressChange" :visible-item-count="5"></mt-picker>
    </div>
  </mt-popup>
</template>

<script>
    import address from '../libs/address';

    export default {
        props: {
            pickerState:{
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                addressProvince: '北京',
                addressCity: '北京',
                showPicker: this.pickerState,
                addressSlots: [
                  {
                    flex: 1,
                    values: Object.keys(address),
                    className: 'slot1',
                    textAlign: 'center'
                  }, {
                    divider: true,
                    content: '-',
                    className: 'slot2'
                  }, {
                    flex: 1,
                    values: ['北京'],
                    className: 'slot3',
                    textAlign: 'center'
                  }
                ]
            };//获取props 里面的值 请用this.xxx
        },
        watch: {
          pickerState(value){
            console.log(value);
            this.showPicker = value;
          },
          // showPicker(curVal,lastVal){//根据关闭picker的操作同步数据
          //   if(curVal === false && lastVal === true){
          //     this.sure();
          //   }
          // }
        },
        methods: {
            onAddressChange(picker, values) {
              console.log('onAddressChange');
                picker.setSlotValues(1, address[values[0]]);
                this.addressProvince = values[0];
                this.addressCity = values[1];
            },
            sure() {
              console.log('sure');
                this.showPicker = false;
                let obj = {
                  province: this.addressProvince,
                  city: this.addressCity,
                  pickerState: this.showPicker
                };
                this.$emit("on-result-change",obj);//子组件向父请求改变状态
            }
        }
    };
</script>
<style lang="scss">
  .mint-popup{
    width: 100%;
  }
  .page-picker-wrapper{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: #fff;
    .picker-item{
      color: #333;
    }
  }
  .toolbar{
    line-height: 0.36rem;
    text-indent: 3rem;
    font-size: 0.16rem;
    // border-bottom: 0.01rem solid #ddd;
    color: #333;
  }

</style>



