<template>
<div class="vue">

    <MyHead></MyHead>

    <div class="main">

        <a @click="goGoods" :data-id="item.id" v-for="item in info" class="box">
            <img :src="item.img" alt="">
            <div class="content">
                <div class="name">{{item.name}}</div>
                <div class="description">{{item.desc}}</div>
                <div class="price">{{item.price}}</div>
            </div>
        </a>

    </div>


    <Tabbar page="store"></Tabbar>
</div>
</template>
<script>
import MyHead from '../components/header';
import Tabbar from '../components/tabbar';
import {
    Indicator
} from 'mint-ui';

export default {
    data() {
        return {
            info: []
        }
    },
    mounted() {
        let cacheStorage = window.window.sessionStorage.store;
        if (cacheStorage) {
            this.info = JSON.parse(cacheStorage);
        } else {
            this.getInfo();
        }
    },
    methods: {
        getInfo() {
            let that = this;
            Indicator.open({
                spinnerType: 'triple-bounce'
            });
            //mock ajax
            setTimeout(() => {
                Indicator.close();
                let info = [{
                    name: '古木生花，疏影花架',
                    desc: '[生活中，世俗外]的哲学意境',
                    img: 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3419316938,3533362669&fm=11&gp=0.jpg',
                    price: '￥300.00',
                    id: '001'
                }, {
                    name: '古木生花，疏影花架',
                    desc: '[生活中，世俗外]的哲学意境',
                    img: 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3419316938,3533362669&fm=11&gp=0.jpg',
                    price: '￥300.00',
                    id: '002'
                }, {
                    name: '古木生花，疏影花架',
                    desc: '[生活中，世俗外]的哲学意境',
                    img: 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3419316938,3533362669&fm=11&gp=0.jpg',
                    price: '￥300.00',
                    id: '003'
                }, ];
                that.info = info;
                window.window.sessionStorage.store = JSON.stringify(info);
            }, 2000);
        },
        goGoods(el) {
            let target = this.targetElement(el);
            this.$router.push({
                path: 'goods/' + target.getAttribute('data-id')
            });
        },
        targetElement(el) {
            let target = el.target;
            while (target.className !== 'box') {
                target = target.parentNode;
                console.log(target);
            }
            return target;
        }
    },
    components: {
        MyHead,
        Tabbar
    }
};
</script>
<style lang="scss" scoped>
img {
    display: block;
}
.main {
    margin-top: 0.44rem;
    box-sizing: border-box;
    padding-bottom: 0.6rem;
    img {
        width: 100%;
        height: 2rem;
    }
    .content {
        position: relative;
        height: 0.9rem;
        .name {
            padding-top: 0.05rem;
            line-height: 0.36rem;
            font-size: 0.16rem;
            text-indent: 0.15rem;
        }
        .description {
            color: #db8747;
            font-size: 0.13rem;
            text-indent: 0.15rem;
        }
        .price {
            position: absolute;
            right: 0.25rem;
            top: 0.25rem;
            font-size: 0.16rem;
            color: #b4282d;
        }
    }
    .content::after {
        content: "";
        position: absolute;
        width: 3.55rem;
        height: 0.01rem;
        background: #ddd;
        bottom: 0.2rem;
        left: 0.1rem;
    }
}
</style>



