<template>

    <div class="header">

      <div class="addr">
        <div class="addressBox">
          <div class="address"  @click="address">{{province}} {{city}}</div>
          <img src="../assets/images/down.png" alt="">
        </div>
        <div class="user" @click="user"></div>
      </div>

      <AddressPicker :pickerState="picker" @on-result-change="onResultChange"></AddressPicker>

    </div>

</template>

<script>
    import AddressPicker from './addressPicker';
    import {
        mapGetters,
        mapActions
    } from 'vuex';

    export default {
        replace: true,
        data() {
            return {
                province: '',
                city: '',
                picker: false,
            };
        },
        computed: {
            ...mapGetters({
              vuexAddress: 'getAddress'
            })
        },
        mounted: function(){
          let that = this;
          // setTimeout(()=>{
            that.initVuexAddress();
          // },3000)
        },
        methods: {
            address() {
                this.picker = !this.picker;
            },
            user() {
                this.$router.push({
                    path: 'user'
                });
            },
            onResultChange(obj){//④外层调用组件方注册变更方法，将组件内的数据变更，同步到组件外的数据状态中
                console.log(obj);
                this.picker = obj.pickerState;
                this.province = obj.province;
                this.city = obj.city;
                this.$store.commit('setAddress', obj);//提交mutation
                console.log(this.vuexAddress.city);
            },
            initVuexAddress(){

                this.province = this.vuexAddress.province;
                this.city = this.vuexAddress.city;
            },
            // ...mapMutations([
            //     'increment' // 映射 this.increment() 为 this.$store.commit('increment')
            // ])
        },
        watch: {
          vuexAddress(val){
            console.log(val);
          }
        },
        components: {
          AddressPicker
        }
    };
</script>
<style lang="scss">
  .addr{
    line-height: 0.44rem;
    height: 0.44rem;
    font-size: 0.18rem;
    text-align: center;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 9999;
    .addressBox{
      font-size: 0.14rem;
      line-height: 0.44rem;
      position: absolute;
      left: 0;
      top: 0;
      padding:  0 0.14rem;

      .address{
        display: inline-block;
        vertical-align: middle;
      }
      img{
        width: 0.2rem;
        height: 0.2rem;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        top: -0.03rem;
      }
    }
    .user{
      width: 0.44rem;
      height: 0.44rem;
      position: absolute;
      top: 0;
      right: 0;
      background: url('../assets/images/user.png') no-repeat center;
      background-size: 0.18rem;
    }
  }
</style>



